import Vue from 'vue';
import Router from 'vue-router';

import LoginPage from '../src/components/Login&Register/LoginPage.vue';
import RegisterPage from '../src/components/Login&Register/RegisterPage.vue';
import HomePage from '../src/components/Pages/Home.vue';
import AddDevicePage from '../src/components/Pages/Add.vue';
import QueryDevicePage from '../src/components/Pages/Query.vue';
import ModifyPage from '../src/components/Pages/Modify.vue';
import ScrapPage from '../src/components/Pages/Scrap.vue';

Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/login',
            name: '登录',
            component: LoginPage,
            meta: {
                keepAlive: false
            }
        },
        {
            path: '/register',
            name: '注册',
            component: RegisterPage,
            meta: {
                keepAlive: false
            }
        },
        {
            path: '/homepage',
            name: '首页',
            component: HomePage,
            meta: {
                keepAlive: true
            }
        },
        {
            path: '/add',
            name: '资产录入',
            component: AddDevicePage,
            meta: {
                keepAlive: true
            }
        },
        {
            path: '/query',
            name: '资产查询',
            component: QueryDevicePage,
            meta: {
                keepAlive: true
            }
        },
        {
            path: '/modify',
            name: '资产信息修改',
            component: ModifyPage,
            meta: {
                keepAlive: true
            }
        },
        {
            path: '/scrap',
            name: '资产废弃',
            component: ScrapPage,
            meta: {
                keepAlive: true
            }
        },
        {
            path: '*',
            redirect: '/login'
        }
    ]
});


