      </div>
    </el-tab-pane>
  </el-tabs>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      page: 1,
      totalRow: 0,
      rowsPerPage: 10,
      tabKey: "0", // 0, 1, 2, 3
      dialogFormVisible: false,
      deleteDialogVisible: false,
      editDialogData: [],
      deleteDialogData: [],
      tableData: []
    };
  },
  beforeMount() {
    let self = this;
    axios
      .get(
        `http://127.0.0.1:8087/admin/serviceItem?rowsPerPage=${self.rowsPerPage}&page=${self.page}`,
        {
          headers: {
            // token: this.$cookie.get("TOKEN")
            'Authorization': `Bearer ${self.$cookie.get('TOKEN')}`
          }
        }
      )
      .then(data => {
        self.totalRow = data.data.total;
        self.tableData = data.data.list;
      })
      .catch(err => {
        self.$notify({
          title: "初始化失败",
          message: "网络连接错误",
          type: "error"
        });
        console.log(err);
      });
  },
  methods: {
    onQuery(current) {
      let self = this;
      self.page = current;
      var url = "";
      if (self.tabKey == 0)
        url = `http://127.0.0.1:8087/admin/serviceItem?rowsPerPage=${self.rowsPerPage}&page=${self.page}`;
      else if (self.tabKey == 1)
        url = `http://127.0.0.1:8087/admin/Device?rowsPerPage=${self.rowsPerPage}&page=${self.page}`;
      else if (self.tabKey == 2)
        url = `http://127.0.0.1:8087/admin/Item?rowsPerPage=${self.rowsPerPage}&page=${self.page}`;
      else
        url = `http://127.0.0.1:8087/admin/retireItem?rowsPerPage=${self.rowsPerPage}&page=${self.page}`;
      console.log(self.tabKey);
      console.log(url);
      axios
        .get(url, {
          headers: {
            // token: this.$cookie.get("TOKEN")
            'Authorization': `Bearer ${self.$cookie.get('TOKEN')}`
          }
        })
        .then(data => {
          self.totalRow = data.data.total;
          self.tableData = data.data.list;
        })
        .catch(err => {
          self.$notify({
            title: "错误",
            message: "网络连接错误",
            type: "error"
          });
          console.log(err);
        });
    },
    handleModify(data) {
      this.dialogFormVisible = true;
      this.editDialogData = data;
    },
    handleModifyConfirm() {
      this.dialogFormVisible = false;
      let self = this;
      axios.post('http://127.0.0.1:8087/admin/editItem', {
        itemId: self.editDialogData.item_id,
        keeper: self.editDialogData.keeper,
        supervisor: self.editDialogData.supervisor,
        status: self.editDialogData.status
      }, {
          headers: {
            // 'token': this.$cookie.get('TOKEN')
            'Authorization': `Bearer ${self.$cookie.get('TOKEN')}`
          }
      })
        .then(data => {
          if (data.data.result) {
            self.$notify({
              title: '成功',
              message: '修改成功！',
              type: 'success'
            });
            self.$router.go(0);
          }
          else {
            self.$notify({
              title: '失败',
              message: '修改失败！',
              type: 'warning'
            })
          }
        })
        .catch(err => {
          self.$notify({
            title: '错误',
            message: '网络连接错误',
            type: 'error'
          })
          console.log(err);
        })
    },
    handleDelete(data) {
      this.deleteDialogVisible = true;
      this.deleteDialogData = data;
    },
    handleDeleteConfirm() {
      this.deleteDialogVisible = false;
      let self = this;
      axios.post('http://127.0.0.1:8087/admin/retire', {
        itemId: self.deleteDialogData.item_id, 
        recorder: self.$cookie.get('USERNAME'),
        detail: '页面删除',
      }, {
          headers: {
            // 'token': this.$cookie.get('TOKEN')
            'Authorization': `Bearer ${self.$cookie.get('TOKEN')}`
          }
      })
        .then(data => {
          if (data.data.result) {
            self.$notify({
              title: '成功',
              message: '报废成功！',
              type: 'success'
            });
            self.$router.go(0);
          }
          else {
            self.$notify({
              title: '失败',
              message: '报废失败！',
              type: 'warning'
            })
          }
        })
        .catch(err => {
          self.$notify({
            title: '错误',
            message: '网络连接错误',
            type: 'error'
          })
          console.log(err);
        })
    },
    distinguish() {
      if (this.deleteDialogData.itemType == "device") {
        return "设备";
      } else {
        return "物品";
      }
    },
    deviceDetails() {
      if (this.deleteDialogData.itemType == "device") {
        return `（设备号：${this.deleteDialogData.sn}，mac地址：${this.deleteDialogData.mac}）`;
      } else {
        return "";
      }
    },
    handlePageChange(c) {
      this.onQuery(c);
    }
  }
};
</script>

